.friendList {
    width: 300px;
    height: auto;
    margin: auto auto;
  
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    padding: 0;
    list-style-type: none;
  }
  .item {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    align-items: center;
    margin-top: 15px;
    margin-left: 0;
    padding: 0;
    background-color: #fff;
    box-shadow: 0px 2px 2px 0px black;
  }
  
  .status {
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: green;
    margin: 0 15px 0 15px;
  }
  
  .statusFalse {
    background-color: red;
  }
  
  .avatar {
    width: 100px;
    height: auto;
    border-radius: 15%;
    padding: 10px 0 10px 0;
  }
  
  .name {
    font-family: 'Montserrat';
    font-weight: 400;
    padding-left: 15px;
  }